.head-back {
    background: url("header-back.jpg");
    background-repeat: no-repeat;
    background-size: 1440px 600px;
}

.header-nav {
    background-color:rgba(255, 255, 255, 0.5);
}

.head-text {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 500px;
    text-align: center;
}

.head-form {
    padding:1em;
    border: 1px solid;
    border-radius: 30px;
    background-color: white;
}

.head-button {
    border:1px solid white;
    background-color:#FC7000;
    padding:0.1em; 
    border-radius:100%; 
    margin-right:0.1em;
    margin-left:0.1em;
}

.head-button-image {
    filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(133deg) brightness(108%) contrast(107%);
}

.head-calendar {
    filter: invert(38%) sepia(86%) saturate(1626%) hue-rotate(9deg) brightness(106%) contrast(104%);
}

.head-input {
    border:none;
}

.nav-button {
    filter: invert(18%) sepia(86%) saturate(509%) hue-rotate(174deg) brightness(95%) contrast(94%);
}

.text {
    align-self: center;
    color: white;
}

.brand, .nav-link {
    color:#1B3C68;
}

.insc-link {
    border: 1px solid white;
    background-color: white;
    border-radius: 30px;
}

.insc-link:hover {
    border:none;
    background-color: none;
}