.main-second {
    text-align: center;
    padding: 3em;
}

.second-photo {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: white;
}

.box-photo {
    border-radius: 10px;
    height: 240px;
    width: 278px;
    border: 1px solid transparent;
    margin: 1em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.photo-1 {
    background: url("couche.jpg");
    background-repeat: no-repeat;
    background-size: 278px 240px;
}

.photo-2 {
    background: url("mer.jpg");
    background-repeat: no-repeat;
    background-size: 278px 240px;
}

.photo-3 {
    background: url("camp.jpg");
    background-repeat: no-repeat;
    background-size: 278px 240px;
}
